html {
  scroll-behavior: smooth;
  box-sizing: border-box;
  width: 100%;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
body {
  margin: 0;
  width: 100%;
  min-height: 100%;
  overflow-x: hidden;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  background-color: var(--bg-c-navy);
  color: var(--c-slate);
  line-height: 1.3;
  font-family: var(--font-Calibre);
  font-size: var(--fs-xl);
}
body.main-hidden-206W- {
  overflow: hidden;
}
::selection {
  background-color: var(--highlight);
}
h1,
h2,
h3,
h4,
h5 {
  font-weight: 600;
  color: var(--c-white);
  margin: 0 0 10px 0;
}
#main-root-afOfS {
  min-height: 100vh;
  display: grid;
  grid-template-rows: 1fr auto;
  grid-template-columns: 100%;
}
img {
  width: 100%;
  max-width: 100%;
  vertical-align: middle;
}
svg {
  width: 100%;
  height: 100%;
  fill: currentColor;
  vertical-align: middle;
}
a {
  display: inline-block;
  text-decoration: none;
  text-decoration-skip-ink: auto;
  color: inherit;
  position: relative;
  transition: var(--transition);
  cursor: pointer;
}
a:hover,
a:focus {
  color: var(--c-green);
  outline: 0;
}
button {
  cursor: pointer;
  border: 0;
  border-radius: 0;
}
button:focus,
button:active {
  outline-color: var(--c-blue);
}
input,
textarea {
  border-radius: 0;
  outline: 0;
}
input:focus,
textarea:focus {
  outline: 0;
}
input:focus::placeholder,
input:active::placeholder,
textarea:focus::placeholder,
textarea:active::placeholder {
  opacity: 0.5;
}
p {
  margin: 0 0 15px 0;
}
ul,
ol {
  padding: 0;
  margin: 0;
  list-style: none;
}
