.email-email-aM3y6 {
  width: 40px;
  position: fixed;
  bottom: 0;
  right: 40px;
  color: var(--c-light-slate);
}
.email-email-aM3y6 div {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}
.email-email-aM3y6 div:after {
  content: '';
  display: block;
  width: 1px;
  height: 90px;
  margin: 0 auto;
  background-color: var(--bg-c-light-slate);
}
.email-email-aM3y6 div a {
  font-family: var(--font-SFMono);
  font-size: var(--fs-xs);
  letter-spacing: 0.5px;
  writing-mode: vertical-rl;
  margin: 20px auto;
  padding: 10px;
}
.email-email-aM3y6 div a:hover,
.email-email-aM3y6 div a:focus {
  transform: translateY(-3px);
}
@media (max-width: 1000px) {
  .email-email-aM3y6 {
    right: 25px;
  }
}
@media (max-width: 768px) {
  .email-email-aM3y6 {
    display: none;
  }
}
